let currentLanguage = 'ja'; // 
let languageData = {};

// 
function loadLanguage(lang) {
    fetch(`lang/${lang}.json`)
        .then(response => response.json())
        .then(data => {
            languageData = data;
            applyLanguage();
        })
        .catch(error => {
            console.error(':', error);
        });
}

// 
function applyLanguage() {
    // : 
    const headerTitle = document.getElementById('header-title');
    if (headerTitle) {
        headerTitle.textContent =
            languageData.header_title || "";
    }

    const userInfoHeader = document.querySelector('.user-info h2');
    if (userInfoHeader) {
        userInfoHeader.textContent =
            languageData.user_info_title || "";
    }

    const editProfileButton = document.getElementById('edit-profile');
    if (editProfileButton) {
        editProfileButton.textContent =
            languageData.edit_profile || "";
    }

    const signOutButton = document.getElementById('sign-out');
    if (signOutButton) {
        signOutButton.textContent =
            languageData.sign_out || "";
    }

    // 
}

// 
function setLanguage(lang) {
    currentLanguage = lang;
    loadLanguage(lang);
}

// 
window.addEventListener('load', () => {
    loadLanguage(currentLanguage);
    if (auth.currentUser) {
        requestNotificationPermission();
    }
});

/************************************************
 * 4. 
 ************************************************/
function showError(message) {
    errorMessageDiv.textContent = message;
    errorMessageDiv.style.display = 'block';
    setTimeout(() => {
        errorMessageDiv.textContent = '';
        errorMessageDiv.style.display = 'none';
    }, 5000);
}

function showEditError(message) {
    if (editErrorMessageDiv) {
        editErrorMessageDiv.textContent = message;
        editErrorMessageDiv.style.display = 'block';
        setTimeout(() => {
            editErrorMessageDiv.textContent = '';
            editErrorMessageDiv.style.display = 'none';
        }, 5000);
    } else {
        console.error('edit-error-message ');
    }
}

